<template>

    <div class="auth-form">

        <login-form-step-1
            v-if="loginStep == 1"
            :phoneValue="loginFormData.phone"
            :privacyPolicyCheckboxValue="loginFormData.privacyPolicyChecked"
            @toggle-step-nmb="changeLoginStep(2)"/>

        <login-form-step-2
            v-if="loginStep == 2"
            @toggle-step-nmb="changeLoginStep(1)"/>

        <div class="form-footer-link">
            <p>Впервые у нас?
                <router-link to="/registration" class="link">Регистрация</router-link>
            </p>
        </div>

    </div>

</template>

<script>

    import LoginFormStep1 from "@/components/Auth/LoginFormStep1"
    import LoginFormStep2 from "@/components/Auth/LoginFormStep2"

    export default {
        name: 'Login',
        components: {
            LoginFormStep1,
            LoginFormStep2
        },
        data() {
            return {
                loginFormData: {
                    phone: '',
                    privacyPolicyChecked: true
                },
                loginStep: 1,
            }
        },
        methods: {
            getErrorStatus(status) {
                this.errorStatus = status
                this.errorModal = true
            },
            changeLoginStep(nmb) {
                this.loginStep = nmb
            },
            closeModal() {
                this.errorModal = false
                this.errorStatus = ''
            }
        },
        mounted() {
            this.$store.commit('setInnRegBool', false)
        }
    }
</script>
