<template>

    <form @submit.prevent="singIn"
          class="auth-form-container auth-form-code">

        <div class="form-title">
            <div class="h2">Введите код</div>
            <p>
                Мы отправили код для подтверждения на номер
                <span style="white-space: nowrap">{{authResPhone | VMask('+# ### ###-##-##')}} </span>
                <span class="mob-block">
                    <a @click="$emit('toggle-step-nmb')"
                       href="#"
                       class="link">Изменить</a>
                </span>
            </p>
        </div>

        <phone-code-group @isFilled="checkFilled"/>

        <div class="form-submit">
            <button :disabled="!isFilled || load"
                    :class="load ? 'loading' : ''"
                    class="btn btn-xl btn-block">Войти
            </button>
        </div>

    </form>

</template>

<script>

    import PhoneCodeGroup from "@/components/Inputs/PhoneCodeGroup";

    export default {
        components: {
            PhoneCodeGroup
        },
        data: () => ({
            load: false,
            isFilled: false
        }),
        computed: {
            authResPhone() {
                return this.$store.state.auth.authResPhone
            },
        },
        methods: {
            checkFilled(bool, value) {
                this.isFilled = bool
                this.$store.commit('setAuthResCode', value)
            },
            singIn() {
                this.load = true
                this.$store.dispatch('getCode')
                    .then(resp => {
                        if (resp) {
                            this.$router.push('/').then(() => {
                                this.load = false
                            })
                        }
                        else {
                            this.load = false
                        }
                    })
                    .catch(() => {
                        this.load = false
                    })
            },
        }
    }
</script>
