<template>

    <form @submit.prevent="getCode"
          class="auth-form-container">

        <div class="form-title">
            <div class="h2">Вход</div>
            <p>Введите номер телефона для входа</p>
        </div>

        <fg-input v-model="phoneValueProp"
                  :mask="'+7 ### ### ## ##'"
                  ref="phoneValueInput"
                  label="Номер телефона"
                  :classes="{invalid: $v.phoneValueProp.$error ? (!$v.phoneValueProp.required || !$v.phoneValueProp.minLength) : false }"
                  type="phone"
                  name="login_phone"
                  @input="addCountryCode"
                  placeholder="+7" />

        <fg-checkbox v-model="privacyPolicyCheckboxValueProp">
            Даю согласие на обработку <a href="#" class="link">персональных данных</a>
        </fg-checkbox>

        <div class="login-helper">
            <p>Если необходимо восстановить доступ, обратитесь в службу поддержки по номеру <a class="link" href="tel:8 800 77-518-77">8 800 77-518-77</a></p>
        </div>

        <div class="form-submit">
            <button type="submit"
                    class="btn btn-block btn-xl"
                    :class="load ? 'loading' : ''"
                    :disabled="!privacyPolicyCheckboxValueProp || load">Получить код</button>
        </div>

    </form>

</template>

<script>

    import {required, minLength} from 'vuelidate/lib/validators'

    export default {
        props: {
            phoneValue: [String, Number],
            privacyPolicyCheckboxValue: Boolean,
            loginStep: Number
        },
        validations: {
            phoneValueProp: {required, minLength: minLength(16)},
        },
        data() {
            return {
                load: false,
                phoneValueProp: this.phoneValue,
                privacyPolicyCheckboxValueProp: this.privacyPolicyCheckboxValue,
            }
        },
        methods: {
            changeLoginStep() {
                this.$emit('toggle-step-nmb')
            },
            getCode() {
                if (this.$v.$invalid || !this.privacyPolicyCheckboxValueProp) {
                    this.$v.$touch();
                    return
                }
                this.load = true
                let phone = this.$options.filters.phoneFilter(this.phoneValueProp)
                this.$store.dispatch('getKey', phone)
                    .then(resp => {
                        if (resp) {
                            this.changeLoginStep()
                        }
                        this.load = false

                    })
                    .catch(() => {
                        this.load = false
                    })
            },
            addCountryCode(e) {
                if ( e.indexOf('+7') !== 0 && e.charAt(0) !== '9' ) {
                    this.phoneValueProp = '+7'
                }
            },
        }
    }
</script>
